div#root{
  height: 100%;
  width: 100%;
  padding: 1em;
  background: rgb(255,255,255); /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(255,255,255,1) 1%,rgba(255,255,255,1) 39%,rgba(96,154,201,1) 99%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(1%, rgba(255,255,255,1)),color-stop(39%, rgba(255,255,255,1)),color-stop(99%, rgba(96,154,201,1)));
  background: -o-linear-gradient(top, rgba(255,255,255,1) 1%,rgba(255,255,255,1) 39%,rgba(96,154,201,1) 99%);
  background: linear-gradient(to bottom, rgba(255,255,255,1) 1%,rgba(255,255,255,1) 39%,rgba(96,154,201,1) 99%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#609ac9',GradientType=0 ); /* IE6-9 */
}
div.main{
  padding: 0em;
  height: 100%;
}

div.top{
  font-weight: bold;
  font-size: 2em;
  text-align: center;
}
div.test{
  width: 100%;
}
.right{
  display: inline-block;
  width: 25%;
}
.middle{
  display: inline-block;
  width: 75%;
  height: 100%;
  margin: 0em;
  vertical-align: top;
  text-align: center;
}
.bottom{
  clear: both;
  float: left;
  text-align: center;
  height: 25%;
}
.avgscore{
  color: black;
  font-size: 36px;
  font-weight: bold;
}

button.card, button.cardplayed, button.noVote{
  border-radius: 6px;
  -webkit-box-shadow: 10px 10px 21px -6px rgba(0,0,0,0.75);
  box-shadow: 10px 10px 21px -6px rgba(0,0,0,0.75);
  margin: 4px;
  border: 8px solid antiquewhite;
  width: 120px;
  height: 150px;
  background-color: cornflowerblue;
  color: black;
  font-size: 52px;
}

button.cardplayed{
  background-color: black;
  color: white;
}

button.noVote{
  background-color: transparent;
  color: black;
  border: 2px solid grey;
}

button.card:hover{
  background-color: blue;
  color: white;
  border: 8px solid whitesmoke;
  -webkit-transform: rotate(10deg) translate(0,-25px);
      -ms-transform: rotate(10deg) translate(0,-25px);
          transform: rotate(10deg) translate(0,-25px);
  -webkit-transition: all 0.25s ease-in-out;
  -o-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}
p.votes{
  display: inline;
}

.voteName{
  font-size: 16px;
}
div.username{
  width:50%;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 25%;
}
.btn{
  margin: 6px;
}


